import React, { useCallback } from "react";

const formulas = [
  {
    name: "ARBOR_SCREEN",
    syntax: "=ARBOR_SCREEN(query)",
    description: "Screen companies based on your query",
    example: '=ARBOR_SCREEN("Apple Supply Chain Companies Stock")',
  },
  {
    name: "ARBOR_DEEP",
    syntax: "=ARBOR_DEEP(ticker, query, [isLongAnswer])",
    description: "Ask questions about a specific company",
    example: '=ARBOR_DEEP("AAPL", "What were the key highlights from their latest earnings call?")',
  },
  {
    name: "ARBOR_FAST",
    syntax: "=ARBOR_FAST(ticker, query)",
    description: "Get factual information about a company",
    example: '=ARBOR_FAST("GOOGL", "Who is their current CEO?")',
  },
  {
    name: "ARBOR_NUMBER",
    syntax: "=ARBOR_NUMBER(ticker, query)",
    description: "Get numerical answers about a company",
    example: '=ARBOR_NUMBER("MSFT", "What was their revenue growth rate in 2023?")',
  },
];

const Instructions = () => {
  const handleFormulaClick = useCallback(async (formula: string) => {
    try {
      await Excel.run(async (context) => {
        const range = context.workbook.getSelectedRange();
        const cell = range.getCell(0, 0);
        cell.formulas = [[formula]];
        await context.sync();
        cell.calculate();
      });
    } catch (error) {
      console.error("Error inserting formula:", error);
    }
  }, []);

  return (
    <div className="p-4">
      <h2 className="text-lg font-semibold mb-4">Available Formulas</h2>

      <div className="space-y-4">
        {formulas.map((formula) => (
          <div key={formula.name} className="py-3">
            <code className="font-mono text-black">{formula.syntax}</code>
            <p className="text-sm text-darkGrey mt-1">{formula.description}</p>
            <p
              className="text-xxs text-royalBlue mt-1 cursor-pointer py-2"
              onClick={() => handleFormulaClick(formula.example)}
            >
              {formula.example}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Instructions;
