import React, { useState } from "react";
import { UseFormRegister } from "react-hook-form";

interface TokenInputProps {
  id: string;
  label: string;
  register: UseFormRegister<any>;
  className?: string;
}

export const TokenInput: React.FC<TokenInputProps> = ({ id, label, register, className }) => {
  const [showToken, setShowToken] = useState(false);

  return (
    <div className="md:block duration-200 mx-2 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-4">
      <label htmlFor={id} className="items-center font-title text-sm px-4 mb-2">
        {label}
      </label>
      <div className="relative">
        <input
          className={`placeholder-color-[#9A99A1] w-full rounded-full border border-[#d9d8dd] px-6 py-4 pr-14 ${className}`}
          type={showToken ? "text" : "password"}
          id={id}
          {...register(id)}
        />
        <button
          type="button"
          className="absolute right-4 top-1/2 -translate-y-1/2 text-royalBlue"
          onClick={() => setShowToken(!showToken)}
        >
          {showToken ? "Hide" : "Show"}
        </button>
      </div>
    </div>
  );
};
