import React, { FC, useEffect, useState } from "react";
import { ARBOR_CHAT_API } from "../taskpane";
import axios from "axios";

interface QuotaData {
  used: number;
  pending: number;
  totalLimit: number;
  start: number;
  end: number;
}

const QuotaBar: FC<{ apiHost: string; token: string }> = ({ apiHost, token }) => {
  const [quota, setQuota] = useState<QuotaData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [tokenInvalid, setTokenInvalid] = useState(false);

  const fetchQuota = async () => {
    try {
      setIsRefreshing(true);
      const quotaUrl = `${apiHost || ARBOR_CHAT_API}/api/v1/quota`;

      const response = await axios.get(quotaUrl, {
        headers: {
          "X-API-KEY": token,
        },
      });

      const data = response.data;

      if (!data.success) {
        setError("API Key is invalid");
        setTokenInvalid(true);
        return;
      }

      setQuota(data.result);
      setTokenInvalid(false);
      setError(null);
    } catch (err) {
      setError("API Key is invalid");
      setTokenInvalid(true);
    } finally {
      setIsRefreshing(false);
    }
  };

  useEffect(() => {
    fetchQuota();
    if (!tokenInvalid) {
      // Refresh quota every 5 minutes
      const interval = setInterval(fetchQuota, 60000 * 5);
      return () => clearInterval(interval);
    }
    return () => {};
  }, [token, apiHost, tokenInvalid]);

  if (error !== null) {
    return (
      <div className="md:block duration-200 mx-2 px-6 mb-4 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-4">
        <div className="flex justify-between items-center">
          <div className="text-red-500">{error}</div>
        </div>
      </div>
    );
  }

  if (!quota) {
    return null;
  }

  const usedPercentage = (quota.used + quota.pending) / quota.totalLimit;

  return (
    <div className="md:block duration-200 mx-2 px-6 mb-4 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-4">
      <div className="flex justify-between items-center">
        <div className="text-sm">API Usage:</div>
        <div className="flex items-center gap-2">
          <div className="text-sm">
            {quota.used + quota.pending} / {quota.totalLimit}
          </div>
          <button
            onClick={fetchQuota}
            disabled={isRefreshing}
            className="p-1 rounded hover:bg-gray-100 disabled:opacity-50"
            title="Refresh quota"
          >
            <svg
              className={`w-4 h-4 ${isRefreshing ? "animate-spin" : ""}`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
              />
            </svg>
          </button>
        </div>
      </div>
      <div className="w-full rounded-lg h-2 border border-darkGrey my-2 text-[0px]">
        <div
          className="bg-darkGrey h-full rounded-lg transition-all duration-300"
          style={{
            width: `${Math.min(usedPercentage, 100)}%`,
          }}
        />
      </div>
    </div>
  );
};

export default QuotaBar;
