import { create } from "zustand";

interface ConsoleState {
  logs: string[];
  addLog: (log: string) => void;
  clear: () => void;
}

const useConsoleStore = create<ConsoleState>((set) => ({
  logs: [],
  addLog: (log: string) => set((state) => ({ logs: [...state.logs, log] })),
  clear: () => set({ logs: [] }),
}));

// Override console.log to store logs
const originalConsoleLog = console.log;
console.log = (...args) => {
  originalConsoleLog.apply(console, args);
  useConsoleStore
    .getState()
    .addLog(
      "[Log] " + args.map((arg) => (typeof arg === "object" ? JSON.stringify(arg, null, 2) : String(arg))).join(" ")
    );
};

const originalConsoleError = console.error;
console.error = (...args) => {
  originalConsoleError.apply(console, args);
  useConsoleStore
    .getState()
    .addLog(
      "[Err] " + args.map((arg) => (typeof arg === "object" ? JSON.stringify(arg, null, 2) : String(arg))).join(" ")
    );
};

export default useConsoleStore;
