import React from "react";
import { UseFormRegister } from "react-hook-form";

interface ModelProviderProps {
  register: UseFormRegister<any>;
  hide: boolean;
}

export const ModelProviderSelect = ({ register, hide }: ModelProviderProps) => {
  if (hide) {
    return null;
  }
  return (
    <div className="md:block duration-200 mx-2 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-4">
      <label className="items-center font-title text-sm px-4 mb-2">Model Provider</label>
      <div className="flex gap-4 px-4">
        <label className="flex items-center gap-2">
          <input type="radio" value="openai" {...register("modelProvider")} />
          OpenAI
        </label>
        <label className="flex items-center gap-2">
          <input type="radio" value="groq" {...register("modelProvider")} />
          Groq
        </label>
      </div>
    </div>
  );
};
