import React, { useState } from "react";
import { getFromLocalStorage, setInLocalStorage, getFromWorkbookSettings, setInWorkbookSettings } from "../taskpane";
import { useForm } from "react-hook-form";
import QuotaBar from "./QuotaBar";
import { TokenInput } from "./TokenInput";
import Instructions from "./Instructions";
import { ModelProviderSelect } from "./ModelProviderSelect";
import { HostOverrideInput } from "./HostOverrideInput";
import ConsoleView from "./ConsoleView";
// import store from "../../stores/store";

type FormInputs = {
  apiKey: string;
  apiHost: string;
  disableCalculationOverride: boolean;
  modelProvider: "groq" | "openai";
};

const loadSettings = async () => {
  const apiKey = getFromLocalStorage("apiKey") || getFromLocalStorage("arborApiToken") || "";

  const apiHostStr = getFromLocalStorage("apiHost");
  const apiHost = apiHostStr && apiHostStr.startsWith("http") ? apiHostStr : "";
  // Load calculation override setting
  const disableCalculationOverride = (await getFromWorkbookSettings("disableCalculationOverride")) === "true";

  const modelProvider = ((await getFromWorkbookSettings("modelProvider")) as "groq" | "openai") || "openai";

  return { apiKey, apiHost, disableCalculationOverride, modelProvider };
};

const App = () => {
  // const { requestPendingCount, requestRunningCount } = useSyncExternalStore(
  //   store.subscribe.bind(store),
  //   () => store.getState()
  // );
  const [advanceSettings, setAdvanceSettings] = useState(false);
  const {
    register,
    handleSubmit,
    watch,
    formState: { isDirty, isSubmitting },
    reset,
  } = useForm<FormInputs>({
    defaultValues: async () => await loadSettings(),
  });

  const onSubmit = async (data: FormInputs) => {
    try {
      setInLocalStorage("apiKey", data.apiKey);
      setInLocalStorage("apiHost", data.apiHost);
      await setInWorkbookSettings("disableCalculationOverride", data.disableCalculationOverride.toString());
      await setInWorkbookSettings("modelProvider", data.modelProvider);
      reset({
        apiKey: data.apiKey,
        apiHost: data.apiHost,
        disableCalculationOverride: data.disableCalculationOverride,
        modelProvider: data.modelProvider,
      });
    } catch (error) {
      console.error("Error saving keys:", error);
    }
  };

  return (
    <div className="pt-2 px-2">
      <div className="flex min-h-[100dvh] w-screen max-w-full flex-col">
        <div className="flex flex-1 gap-4 p-0 md:p-4">
          <div className="h-full w-full md:block relative">
            <div className="w-full h-full">
              <div className="sticky top-0 flex h-full w-full shrink-0 grow-0 flex-col gap-2 overflow-auto rounded-lg bg-lightGrey py-4 px-4">
                <a
                  className="w-full flex-0 relative mb-6 block h-8 w-24 my-4 flex items-center justify-center"
                  href="https://arborchat.ai/"
                >
                  <img
                    alt="logo"
                    width="96"
                    height="32"
                    decoding="async"
                    data-nimg="1"
                    style={{ color: "transparent" }}
                    src="/assets/logo.png"
                  />
                </a>
                {/* <p>Pending: {requestPendingCount}</p>
                <p>Running: {requestRunningCount}</p> */}
                <form onSubmit={handleSubmit(onSubmit)}>
                  <div className="">
                    <TokenInput
                      id="apiKey"
                      label="Arbor API Key"
                      register={register}
                      className="placeholder-color-[#9A99A1]"
                    />
                  </div>
                  {!isDirty && watch("apiKey") && <QuotaBar apiHost={watch("apiHost")} token={watch("apiKey")} />}
                  <div className="md:block duration-200 mx-2 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-4">
                    <label className="font-title text-sm px-4 mb-2 flex gap-2">
                      <input type="checkbox" {...register("disableCalculationOverride")} />
                      <span className="text-xs">Disable Calculation Manual Override</span>
                    </label>
                  </div>
                  {advanceSettings && (
                    <>
                      <HostOverrideInput id="apiHost" label="API Host Override" register={register} />
                      <ModelProviderSelect register={register} hide={false} />
                    </>
                  )}
                  <div className="">
                    <div className="md:block duration-200 mx-2 whitespace-nowrap rounded-lg p-2 font-title text-sm transition-colors mb-8">
                      <button
                        type="submit"
                        className={`w-full rounded-full bg-[#162332] px-6 py-4 font-semibold text-[#ffffff] ${isSubmitting || !isDirty ? "bg-[#646b73]" : ""}`}
                        disabled={isSubmitting || !isDirty}
                      >
                        {isSubmitting ? "Saving..." : "Save"}
                      </button>
                    </div>
                  </div>
                </form>
                <Instructions />
                {advanceSettings && <ConsoleView />}
                <div>1.0.0 dev</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="pb-2" onClick={() => setAdvanceSettings(!advanceSettings)}></div>
    </div>
  );
};

export default App;
